import React, { useState, useRef } from 'react';
import './Formular.css';
import { useForm, SubmitHandler } from "react-hook-form";
import emailjs from '@emailjs/browser';
import { useNavigate } from "react-router-dom";


export interface IFormularInput {
  name: string;
  email: string;
  phoneNumber: string;
  nameFirma: string;
  street: string;
  streetNumber: string;
  city: string;
  psc: string;
  country: string;
  note?: string; 
  ico: string;
}

function Formular(props: {package: string, isOrdered: boolean, setIsOrdered: React.Dispatch<React.SetStateAction<boolean>>}){

  const {
    register,
    formState: { errors },
    handleSubmit,
    } = useForm<IFormularInput>({})
  
  const form: React.MutableRefObject<any> = useRef(null);

  const sendEmail = (e: { preventDefault: () => void; }) => {
    e.preventDefault(); // prevents the page from reloading when you hit Send
 
    emailjs.sendForm('service_1rwmo33', 'template_ms2whia', form.current, 'AUoONSgGlY9CmbHE2')
      .then((result) => {
          console.log(result);
      }, (error) => {
          console.log(error);
      });

      props.setIsOrdered(true);
  };

  const navigate = useNavigate();

  const navigateToPage = (url: string) => {
    navigate(url);
  };

  return (
    <div>
      <div className='formular-container' id="formular">
        {/* <div className='formular-title main-title'>Formulr</div> */}
        <form ref={form} onSubmit={sendEmail} className="form">
          <input className='enable-display' name="package" value={props.package}/>
          <div className='grid-box'>
            
              <div className='x-area'>
                <div className='title title-form'>Osobn daje</div>

                <div className="form-name">
                  <input type="text" id="name" required placeholder='Meno a priezvisko' className='info-input'
                  {...register("name", { required: true, maxLength: 100, pattern: /[A-Za-z]* [A-Za-z]*/ })} />
                  {errors.name && (
                  <p className="formular__error">Name has wrong format.</p>
                )}
                </div>

                <div className="form-email">
                  <input type="email" id="email" required placeholder='Email' className='info-input'
                  {...register("email", {  required: true, maxLength: 100, pattern: /[A-Za-z1-9]*@[A-Za-z1-9]*/})}
                />
                {errors.email && (
                  <p className="formular__error">Email has wrong format.</p>
                )}
                </div>

                <div className="form-tel">
                  <input type="tel" id="tel" placeholder='Telefn' className='info-input'
                  {...register("phoneNumber", {
                      required: true,
                      pattern: /^[0-9\b]+$/,
                    })}
                  />
                {errors.phoneNumber && (
                  <p className="formular__error">Phone number has wrong format.</p>
                )}
                </div>
              </div>
              

              <div className='y-area'>
                <div className='title title-form'>Firemn daje</div>
                <div className="form-nameFirma">
                  <input type="text" id="firma" required placeholder='Nzov spolonosti' className='info-input'
                  {...register("nameFirma", { required: true, maxLength:100})}/>
                  {errors.nameFirma &&(
                    <p className='formular__error'>Meno firmy je nesprvne vyplnen.</p>
                  )}
                </div>
                <input type="number" id="ico" required placeholder='IO' className='info-input'
                  {...register("ico", { required: true, maxLength:8, minLength:8})}/>
                  {errors.nameFirma &&(
                    <p className='formular__error'>IO firmy je nesprvne vyplnen.</p>
                  )}
                </div>
            </div>
            
            {/* <div>
              <div className='y-area'>
                <div className='title title-form'>Adresa</div>

                <div className="form-example">
                  <input type="text" id="street" required placeholder='Ulica' className='info-input'
                  {...register("street", {required:true})}/>
                  {errors.street &&(
                    <p className='formular__error'>Ulica je nesprvne vyplnen.</p>
                  )}
                </div>

                <div className="form-streetNumber">
                  <input type="streetNumber" id="streetNum" required placeholder='slo'  className='info-input'
                  {...register("streetNumber", {required:true})}/>
                  {errors.streetNumber &&(
                    <p className='formular__error'>slo ulice je nesprvne vyplnen.</p>
                  )}
                </div>

                <div className="form-city">
                  <input type="text" id="city" required placeholder='Mesto' className='info-input'
                  {...register("city", { required:true })}/>
                  {errors.city && (
                    <p className='formular__error'>Mesto je nesprvne vyplnen.</p>
                  )}
                </div>

                <div className="form-psc">
                  <input type="number" id="psc" required placeholder='PS' className='info-input'
                  {...register("psc", { required:true })}/>
                  {errors.psc && (
                    <p className='formular__error'>PS je nesprvne vyplnen.</p>
                  )}
                </div>

                <div className="form-example">
                  <input type="text" id="country" required placeholder='tt' className='info-input'
                  {...register("country", { required:true })}/>
                  {errors.country && (
                    <p className='formular__error'>Krajina je nesprvne vyplnen.</p>
                  )}
                </div>
              </div>
              </div> */}
            {/* </div> */}

            <div className='form-checkbox'>
                <div className='checkbox'><input type='checkbox' id='asistantChoice' name="assistant" required={true}/>
                <label htmlFor='asistantChoice'>Shlasm s <div className='anchor-data' onClick={() => navigateToPage('/ochrana-udajov')}>ochranou osobnch dajov</div></label></div>
                <div className='checkbox'><input type='checkbox' id='asistantChoice' name="assistant" required={true}/>
                <label htmlFor='asistantChoice'>Shlasm s <div className='anchor-data' onClick={() => navigateToPage('/obchodne-podmienky')}>obchodnmi podmienkami</div></label></div>
            </div>
            
            <button className='click info-text-click'>Mm zujem</button>
        </form>
      
      </div>

      </div>
);}

export default Formular;