import React from 'react';
import ReactDOM from 'react-dom/client';
import reportWebVitals from './reportWebVitals';
import { createBrowserRouter, RouterProvider  } from 'react-router-dom'; 
import './index.css';
import App from './App';
import ErrorPage from './ErrorPage';
import BasicPackagePage from './BasicPackagePage';
import MediumPackagePage from './MediumPackagePage';
import ExpandedPackagePage from './ExpandedPackagePage';
import PersonalDataPage from './PersonalDataPage';
import BusinessCondPage from './BusinessCondPage';
import ComplaintCondPage from './ComplaintCondPage';

const router = createBrowserRouter([
  {
    path: '/',
    element: <App/>,
    errorElement: <ErrorPage/>,
  },
  {
    path: '/formular-zakladny',
    element: <BasicPackagePage/>,
    errorElement: <ErrorPage/>,
  },
  {
    path: '/formular-stredny',
    element: <MediumPackagePage/>,
    errorElement: <ErrorPage/>,
  },
  {
    path: '/formular-rozsireny',
    element: <ExpandedPackagePage/>,
    errorElement: <ErrorPage/>,
  },
  {
    path: '/ochrana-udajov',
    element: <PersonalDataPage/>,
    errorElement: <ErrorPage/>,
  },
  {
    path: '/obchodne-podmienky',
    element: <BusinessCondPage/>,
    errorElement: <ErrorPage/>,
  },
  {
    path: '/reklamacne-podmienky',
    element: <ComplaintCondPage/>,
    errorElement: <ErrorPage/>,
  },
]);

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
